.nbe-graph {
   background: #eeeeee;
   box-sizing: border-box;
   margin: 2px;
}
.nbe-graph:focus {
   margin-top: 1px;
   margin-left: 1px;
   border: 1px dotted #646464;
   outline: none;
}
.nbe-graph.nbe-highlight-inbound .nbe-ports.nbe-outbound .nbe-port-handle
.nbe-graph.nbe-highlight-outbound .nbe-ports.nbe-inbound .nbe-port-handle {
   background-color: #646464;
}

.nbe-graph-viewport {
   position: absolute;
   overflow: auto;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
}

.nbe-graph-contents {
   position: relative;
}

.nbe-graph-nodes {
   position: relative;
   z-index: 1;
}

.nbe-node,
.nbe-edge-icon {
  position: absolute;
}

.nbe-links {
   position: relative;
   left: 0;
   top: 0;
   width: 100%;
   height: 100%;
   z-index: 0;
}

.nbe-graph-viewport {
   box-shadow: inset 0 1px 2px 1px #a0a0a0;
}

.nbe-graph,
.nbe-graph * {
   user-select: none;
   cursor: default;
}

.nbe-graph *::selection {
   background: transparent;
}

.nbe-graph *::-moz-selection {
   background: transparent;
}

.nbe-graph *::-webkit-selection {
   background: transparent;
}

.nbe-graph-canvas {
   background: linear-gradient(0deg, transparent 24%, #e0e0e0 25%, #e0e0e0 26%, transparent 27%, transparent 74%, #e0e0e0 75%, #e0e0e0 76%, transparent 77%, transparent), linear-gradient(90deg, transparent 24%, #e0e0e0 25%, #e0e0e0 26%, transparent 27%, transparent 74%, #e0e0e0 75%, #e0e0e0 76%, transparent 77%, transparent);
   width: 100%;
   height: 100%;
}

.nbe-link {
   position: absolute;
   fill: transparent;
}

.nbe-ghost {
   position: absolute;
}

.nbe-edge {
   display: block;
   width: 20px;
   height: 40px;
}

.nbe-edge-icon {
   background: #ef6c00;
}

.nbe-edge-label {
   position: relative;
   text-align: center;
   font-family: "Roboto", "Arial", sans-serif;
}

.nbe-port-handle {
   background: #ef6c00;
   display: inline-block;
   position: relative;
}
.nbe-port-handle:hover {
   border-color: #f9f9f9;
}

.nbe-vertex {
   box-shadow: 0 1px 3px 0 #a0a0a0;
   border-radius: 2px;
   background: white;
}
.nbe-vertex.nbe-ping {
   animation-name: nbe-vertex-ping;
   animation-duration: 0.3s;
   animation-iteration-count: 1;
}

.nbe-vertex-header {
   border-radius: 2px 2px 0 0;
   text-align: center;
   font-family: "Roboto", "Arial", sans-serif;
   font-weight: bold;
   color: #fff;
}

.nbe-port-group {
   display: table;
   width: 100%;
   border-top: solid #f9f9f9 1px;
}

.nbe-ports {
   display: table-cell;
   min-width: 10%;
}

.nbe-inbound {
  text-align: left;
}
.nbe-inbound .nbe-port {
   padding-right: 9px;
}

.nbe-outbound {
  text-align: right;
}
.nbe-outbound .nbe-port {
   padding-left: 9px;
}

.nbe-port {
   padding-top: 0;
   color: #202020;
   font-family: "Roboto", "Arial", sans-serif;
   white-space: nowrap;
}


