/* MINIMAP */

.nbe-minimap {
   background: rgba(255, 255, 255, 0.95);
   position: absolute;
   left: 10px;
   bottom: 20px;
   box-shadow: 0 2px 2px #a0a0a0;
   border: 1px solid orange;
   z-index: 200;
   width: 200px;
   height: 100px;
   overflow: hidden;

   opacity: 1;
   visibility: visible;
   transition: visibility 0s linear, opacity 0.5s linear;
}

.nbe-minimap.nbe-hidden {
  opacity: 0;
  visibility: hidden;
  transition: visibility .5s, opacity 0.5s linear;
}

.nbe-minimap .nbe-minimap-handle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 16px 16px 0;
  border-color: transparent orange transparent transparent;

  position: absolute;
  top: 0;
  right: 0;
}

.nbe-minimap .nbe-minimap-viewport {
   position: absolute;
   background: rgba(192, 128, 0, 0.25);
}

.nbe-minimap svg {
  position: absolute;
  left: 0;
  top: 0;
  width: 200px;
  height: 100px;
}

.nbe-minimap-edges circle {
  fill: #ef6c00;
  stroke-width: 0;
}

.nbe-minimap-vertices .nbe-minimap-vertex {
   stroke-width: 2px;
   stroke: #bbb;
}

.nbe-minimap .nbe-minimap-links .nbe-link {
   stroke-width: 4;
}




/* SELECTION */

.nbe-selection {
   border: 1px dashed #e91e63;
   background: rgba(233, 30, 99, 0.1);
   position: absolute;
   top: 10px;
   left: 10px;
   width: 100px;
   height: 100px;
}

