@charset "UTF-8";


html, body {
   height: 100%;
}

.tools-layout .app-toolbar {
   height: 35px;
}

.tools-layout .nav {
   height: 42px;
}

.tools-layout .app-tab {
   height: calc( 100vh - 42px );
   position: relative;
}

.tools-layout .app-tab-content {
   border: 1px solid #ddd;
   border-top: none;
   padding: 20px;
   margin-bottom: 20px;
}



